<template>
  <div class="container">
    <div class="section1" v-if="show">
      <!-- <div class="add-item" @click="addItem">
        <van-icon name="add-o" size="30" color="#1989fa" />
      </div> -->
      <div class="item" v-for="(item, index) in itemList" :key="index">
        <div class="upload-img">
          <p style="font-size: 14px; padding-bottom: 10px">来料图片</p>
          <van-uploader
            v-model="fileList"
            max-count="1"
            :after-read="afterRead"
          />
        </div>
        <van-cell-group>
          <van-dropdown-menu v-if="role > 0">
            <van-dropdown-item
              @change="changeStore(index)"
              v-model="value1"
              :options="option1"
            />
          </van-dropdown-menu>
          <van-field
            v-else
            v-model="userName"
            label="客户名称"
            placeholder="请输入客户名称"
            @input="changeUserName(index)"
          />

          <van-field
            v-model="item.name"
            label="产品名称"
            placeholder="请输入产品名称"
          />
          <van-field
            v-model="item.material"
            label="来料材质"
            placeholder="请输入来料材质"
          />
          <div class="flex-sa">
            <van-field
              v-model="item.number"
              label="来料数量"
              placeholder="值"
              type="number"
            />
            <van-field
              v-model="item.com_number"
              label="交付数量"
              placeholder="值"
              type="number"
            />
          </div>
          <div class="flex-sa">
            <van-field
              v-model="item.length"
              label="来料长(毫米)"
              placeholder="值 "
              type="number"
            />
            <van-field
              v-model="item.width"
              label="来料宽(毫米)"
              placeholder="值"
              type="number"
            />
          </div>
          <div class="flex-sa">
            <van-field
              v-model="item.inch_length"
              label="坑纸长(英寸)"
              placeholder="值 "
              type="number"
            />
            <van-field
              v-model="item.inch_width"
              label="坑纸宽(英寸)"
              placeholder="值"
              type="number"
            />
          </div>
          <van-field
            v-model="item.factory"
            label="印刷工厂"
            placeholder="请输入印刷工厂"
          />
          <van-cell
            title="交货日期"
            :value="item.com_time"
            @click="Clickshowtime(index)"
          />

          <van-calendar
            color="#40b9ec"
            v-model="showtime"
            placeholder="点击选择交货日期"
            @confirm="onConfirmtime"
          />

          <van-cell title="是否回厂">
            <template #right-icon>
              <van-radio-group v-model="item.is_return" direction="horizontal">
                <van-radio name="1">是</van-radio>
                <van-radio name="0">否</van-radio>
              </van-radio-group>
            </template>
          </van-cell>
          <van-cell
            v-if="item.is_return == 1"
            title="回厂时间"
            :value="item.return"
            @click="Clickreturntime(index)"
          />
          <van-calendar
            color="#40b9ec"
            v-model="returntime"
            placeholder="点击选择交货日期"
            @confirm="onrConfirmtime"
          />
          <van-field
            v-model="item.remark"
            rows="2"
            autosize
            label="备注"
            type="textarea"
            maxlength="50"
            placeholder="请输入备注"
            show-word-limit
          />

          <van-collapse v-model="activeNames">
            <block v-for="(sitem, sindex) in item.processList" :key="sindex">
              <van-collapse-item
                :title="sitem.pname"
                :name="sindex"
                v-if="sitem.is_yes == 0"
              >
                <van-radio-group v-model="sitem.next_level_id">
                  <van-field
                    v-model="items.text"
                    center
                    clearable
                    :label="items.pname"
                    placeholder="备注"
                    v-for="(items, indexs) in sitem.next_level"
                    :key="indexs"
                  >
                    <template #button>
                      <van-radio
                        :name="items.pid"
                        @click="onInput(items.checked, index, sindex, indexs)"
                      />
                    </template>
                  </van-field>
                </van-radio-group>
              </van-collapse-item>

              <van-field
                v-else
                v-model="sitem.text"
                center
                clearable
                :label="sitem.pname"
                placeholder="备注"
              >
                <template #button>
                  <van-switch
                    @input="onsInput(sitem.checked, index, sindex)"
                    :value="sitem.checked"
                    size="24"
                  />
                </template>
              </van-field>
            </block>
          </van-collapse>
        </van-cell-group>

        <div class="flex-sa">
          <div @click="savePro(index)" class="save-item">保存</div>
        </div>
      </div>

      <!-- <div class="add-btn">
        <van-button size="normal" @click="switchStatus" block round type="info"
          >添加来料工艺流程</van-button
        >
      </div> -->
    </div>
    <div class="section2" v-if="!show">
      <div class="item">
        <van-collapse v-model="activeNames">
          <van-collapse-item
            :title="sitem.name"
            :name="sindex"
            v-for="(sitem, sindex) in saveItemList"
            :key="sindex"
          >
            <van-cell
              center
              :title="item.pname"
              v-for="(item, index) in sitem.processList"
              :key="index"
            >
              <template #right-icon>
                <van-switch
                  :value="item.checked"
                  @input="onInput(item.checked, sindex, index)"
                  size="24"
                />
              </template>
            </van-cell>
          </van-collapse-item>
        </van-collapse>
      </div>
      <!-- <div class="item">
        <van-field name="radio" label="复用第一个">
          <template #input>
            <van-radio-group v-model="radio" direction="horizontal">
              <van-radio name="1">是</van-radio>
              <van-radio name="2">否</van-radio>
            </van-radio-group>
          </template>
        </van-field>
      </div> -->

      <div class="add-btn">
        <van-button @click="switchStatus" size="normal" block round type="info"
          >返回上一步</van-button
        >
        <van-button @click="subPro" size="normal" block round type="info"
          >提交</van-button
        >
      </div>
    </div>
  </div>
</template>

<script>
import {
  addPorder,
  getPro,
  addPcart,
  getUsers,
  Fileuploader,
} from "@api/public";
import {
  Button,
  DropdownMenu,
  DropdownItem,
  Calendar,
  RadioGroup,
  Toast,
  Uploader,
  Switch,
  Cell,
  Radio,
  Collapse,
  Field,
  CollapseItem,
  Icon,
} from "vant";
import localStorage from "@utils/store/localStorage";
import Loadings from "@components/Loadings";
export default {
  name: "Index",
  components: {
    [Calendar.name]: Calendar,
    [DropdownMenu.name]: DropdownMenu,
    [Toast.name]: Toast,
    [DropdownItem.name]: DropdownItem,
    [Button.name]: Button,
    [CollapseItem.name]: CollapseItem,
    [Cell.name]: Cell,
    [Uploader.name]: Uploader,
    [Collapse.name]: Collapse,
    [RadioGroup.name]: RadioGroup,
    [Radio.name]: Radio,
    [Switch.name]: Switch,
    [Field.name]: Field,
    [Icon.name]: Icon,
    Loadings,
  },
  props: {},
  data: function () {
    return {
      fileList: [],
      value1: 0,
      showtime: false,
      image: "",
      returntime: false,
      option1: [{ text: "请选择客户", value: 0 }],
      itemList: [
        {
          factory: "",
          name: "",
          material: "",
          number: "",
          length: "",
          width: "",
          remark: "",
          user_name: "",
          com_number: "",
          inch_length: "",
          inch_width: "",
          is_return: "0",
          uid: "",
          com_time: "点击选择",
          return: "点击选择",
          id: "",
          image: "",
          processList: [],
          status: 1,
        },
      ],
      saveItemList: [],
      showPicker: false,
      processList: [],
      activeNames: [],
      show: true,
      pshow: true,
      jsonData: null,
      uid: 0,
      userName: "",
      userList: [],
      role: 0,
      editInd: 0,
      comTime: "",
      clickStatus: true,
    };
  },
  created() {
    var jsonData = JSON.parse(localStorage.get("userInfo"));
    this.jsonData = jsonData;

    getPro({})
      .then(({ data }) => {
        data.forEach((item) => {
          item.checked = false;
          item.text = "";
          item.next_level_id = "";
          item.next_level_ind = "-1";
          item.next_level.forEach((sitem) => {
            sitem.checked = false;
            sitem.text = "";
          });
        });

        this.processList = data;
        var itemList = this.itemList;
        this.itemList[0]["processList"] = data;
        this.itemList = itemList;
      })
      .catch((e) => {
        Toast(e.msg);
      });
    this.role = jsonData.role;
    if (jsonData.role > 0) {
      getUsers({ type: 0 })
        .then(({ data }) => {
          var option = [{ text: "点击选择客户名称", value: 0 }];
          this.userList = data;
          data.forEach((item, index) => {
            var obj = {};
            obj["text"] = item.signature ? item.signature : item.user_login;
            obj["value"] = index + 1;
            option.push(obj);
          });
          this.option1 = option;
        })
        .catch((e) => {
          Toast(e.msg);
        });
    } else {
      this.uid = jsonData.id;
      this.userName = jsonData.signature
        ? jsonData.signature
        : jsonData.user_login;

      var itemList = this.itemList;
      itemList[0]["status"] = 0;
      itemList[0]["uid"] = jsonData.id;
      itemList[0]["userName"] = jsonData.id;
      itemList[0]["user_name"] = jsonData.signature
        ? jsonData.signature
        : jsonData.user_login;
      this.itemList = itemList;
    }
  },
  mounted: function () {},
  methods: {
    afterRead(file) {
      // 此时可以自行将文件上传至服务器
      var formData = new FormData(); //构造一个 FormData，把后台需要发送的参数添加
      formData.append("file", file.file);
      console.log(file);
      console.log(formData);
      Fileuploader(formData).then(({ data }) => {
        this.$set(
          this.itemList[0],
          "image",
          data.themb ? data.themb : data.url
        );
      });
    },
    changeStore(e) {
      var itemList = this.itemList;
      itemList[e]["uid"] = this.userList[this.value1 - 1].id;
      itemList[e]["user_name"] = this.userList[this.value1 - 1].signature
        ? this.userList[this.value1 - 1].signature
        : this.userList[this.value1 - 1].user_login;
      this.itemList = itemList;
    },
    changeUserName(e) {
      var itemList = this.itemList;
      itemList[e]["userName"] = this.userName;
      itemList[e]["user_name"] = this.userName;
      this.itemList = itemList;
    },
    remove(e) {
      var itemList = this.itemList;
      itemList.splice(e, 1);
      this.itemList = itemList;
    },
    savePro(e) {
      var itemList = this.itemList[e];
      var processList = itemList.processList;
      var processListData = [];
      processList.forEach((item) => {
        var obj = {};
        if (item.is_yes == 1) {
          if (item.checked) {
            obj[item.pid] = item.text;
            processListData.push(obj);
          }
        } else {
          if (item.next_level_ind != "-1") {
            var obj = {};
            obj[item["next_level"][item.next_level_ind]["pid"]] =
              item["next_level"][item.next_level_ind]["text"];
            processListData.push(obj);
          }
        }
      });
      console.log(processListData);
      itemList["data"] = processListData;
      if (!this.clickStatus) {
      } else if (itemList["user_name"] == "") {
        Toast("请输入客户名称");
      } else if (itemList["name"] == "") {
        Toast("请输入产品名称");
      } else if (itemList["material"] == "") {
        Toast(" 请输入产品材质");
      } else if (itemList["number"] == "") {
        Toast(" 请输入产品数量");
      } else if (itemList["com_number"] == "") {
        Toast(" 请输入交付数量");
      } else if (itemList["width"] == "") {
        Toast("请输入产品宽度 ");
      } else if (itemList["length"] == "") {
        Toast("请输入产品长度 ");
      } else if (itemList["com_time"] == "点击选择") {
        Toast("请选择交货日期 ");
      } else if (
        itemList["is_return"] == "1" &&
        itemList["return"] == "点击选择"
      ) {
        Toast("请选择回厂日期 ");
      } else {
        this.clickStatus = false;
        addPorder(itemList)
          .then(({ data }) => {
            Toast("保存成功");
            location.reload();
          })
          .catch((e) => {
            Toast(e.msg);
            this.clickStatus = true;
          });
      }
    },
    onConfirmtime(date) {
      this.comTime = `${date.getFullYear()}-${
        date.getMonth() + 1
      }-${date.getDate()}`;
      this.showtime = false;
      var itemList = this.itemList;
      itemList[this.editInd]["com_time"] = this.comTime;

      this.itemList = itemList;
    },
    onrConfirmtime(date) {
      this.comTime = `${date.getFullYear()}-${
        date.getMonth() + 1
      }-${date.getDate()}`;
      this.returntime = false;
      var itemList = this.itemList;
      itemList[this.editInd]["return"] = this.comTime;

      this.itemList = itemList;
    },
    Clickshowtime(ind) {
      this.showtime = true;
      this.editInd = ind;
    },
    Clickreturntime(ind) {
      this.returntime = true;
      this.editInd = ind;
    },

    subPro() {
      console.log(this.saveItemList);
      var data = [];
      this.saveItemList.forEach((item) => {
        var obj = {};
        var arr = [];
        obj["oid"] = item.id;
        item.processList.forEach((sitem) => {
          if (sitem.checked) {
            arr.push(sitem.pid);
          }
        });
        obj["proid"] = arr;
        data.push(obj);
      });

      addPcart({ data })
        .then(({ data }) => {
          Toast("提交成功");
          location.reload();
        })
        .catch((e) => {
          Toast(e.msg);
        });
    },
    addItem() {
      if (this.itemList.length == this.saveItemList.length) {
        var list = this.itemList;
        list.push({
          factory: "",
          name: "",
          material: "",
          number: "",
          length: "",
          width: "",
          id: "",
          is_return: "0",
          com_number: "",
          user_name: this.userName,
          uid: this.uid,
          inch_length: "",
          inch_length: "",
          remark: "",
          com_time: "点击选择",
          return: "点击选择",
          processList: this.processList,
        });
        this.itemList = list;
      } else {
        Toast("请先完善好其他来料 再添加");
      }
    },
    switchStatus() {
      if (this.saveItemList.length > 0) {
        this.show = !this.show;
      } else {
        Toast("请先保存好添加完成的来料");
      }
    },
    onInput(e, i, inds, ind) {
      var itemList = this.itemList;
      console.log(666);
      itemList[i]["processList"][inds]["next_level_ind"] = ind;
      this.itemList = itemList;
      console.log(itemList);
    },
    onsInput(e, i, inds) {
      console.log(e);
      console.log(i);
      console.log(inds);
      var itemList = this.itemList;

      itemList[i]["processList"][inds]["checked"] = !e;
      this.itemList = itemList;
      console.log(itemList);
    },
  },
};
</script>
<style scoped>
[v-cloak] {
  display: none;
}
.upload-img {
  background: #fff;
  text-align: center;
  padding-top: 10px;
}
button {
  margin-bottom: 15px;
}
.container {
  background: #f9f9f9;
}
.item {
  padding: 10px 20px 0;
}
.flex-sa {
  background: #fff;
}
.save-item {
  text-align: center;
  margin: 30px 0;
  background: #40b9ec;
  width: 100%;
  color: #fff;
  line-height: 45px;
}
.add-btn {
  margin: 20px;
  margin-top: 40px;
}
.add-item {
  text-align: center;
  padding-top: 10px;
}
.container {
  padding-bottom: 60px;
}
</style>